@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap");
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #FFFFFF;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  font-weight: 400;
  text-align: center;
}

h1 {
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 3.8rem;
  }
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.alinear-der {
  float: right;
}

.alinear-izq {
  float: left;
}

.txt-center {
  text-align: center;
}

.contenido-centrado {
  max-width: 80rem;
  margin: 0 auto;
}

.mostrar-desk {
  display: none;
}
@media (min-width: 768px) {
  .mostrar-desk {
    display: inline-block;
  }
}

.display-none {
  display: none !important;
}

.btn {
  display: inline-block;
  padding: 1rem 2rem;
  border: none;
  text-transform: uppercase;
  font-size: 1.5rem;
  border-radius: 20rem;
}

.btn-active {
  padding: 1rem 3rem;
  background-color: #007bd2;
  color: #FFFFFF;
  border: 1px solid #95adc7;
}
.btn-active:hover {
  background-color: #FFFFFF;
  color: #007bd2;
  border: 1px solid #007bd2;
}

.btn-inactive {
  background-color: #95adc7;
  color: #FFFFFF;
  border: 1px solid #95adc7;
}
.btn-inactive:hover {
  background-color: #FFFFFF;
  color: #007bd2;
  border: 1px solid #007bd2;
}

